import * as React from 'react';
import { Link, NavLink } from 'react-router-dom';

/*
	4/17/2018 4:54:09 PM
	React Scaffolding by Better Software Solutions, LLC (http://bssdev.biz)
*/
export class NavMenu extends React.Component<{}, {}> {
    public render() {
        return <div className='main-nav'>
                <div className='navbar navbar-inverse'>
                <div className='navbar-header'>
                    <button type='button' className='navbar-toggle' data-toggle='collapse' data-target='.navbar-collapse'>
                        <span className='sr-only'>Toggle navigation</span>
                        <span className='icon-bar'></span>
                        <span className='icon-bar'></span>
                        <span className='icon-bar'></span>
                    </button>
                    <Link className='navbar-brand' to={ '/' }>Crud Operation in React Js</Link>
                </div>
                <div className='clearfix'></div>
                <div className='navbar-collapse collapse'>
                    <ul className='nav navbar-nav'>
                        <li>
                            <NavLink to={ '/' } exact activeClassName='active'>
                                <span className='glyphicon glyphicon-home'></span> Home
                            </NavLink>
                        </li>

                    <li>
                        <NavLink to={'/user'} activeClassName='active'>
                            <span className='glyphicon glyphicon-th-list'></span> User
                        </NavLink>
                    </li>

                    </ul>
                </div>
            </div>
        </div>;
    }
}


